.date {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  &__item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;

    width: 50%;
    @include for-size(phone-only) {
      width: 100%;
    }

    & > * {
      width: auto;
      flex-grow: 1;
      @include for-size(phone-only) {
        width: 100%;
      }

      &:first-child {
        margin-right: 10px;
      }
    }
  }
  &__table {
    & td {
      width: 50%;
      padding: 5px 0;
      font-weight: 600;
      vertical-align: top;
      line-height: normal;

      &:last-child {
        text-align: center;
      }
    }
  }
}
