.range-slider {
  -webkit-appearance: none;
  width: 100%;
  height: 5px;
  border-radius: 5px;
  background-color: #e6e6e6;

  outline: none;
}
.range-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 15px;
  height: 15px;
  background-color: $warning-color;
  border-radius: 50%;
  cursor: pointer;
  outline: none;
}

.custom-range {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding-right: 1rem;
  margin-bottom: 1.25rem;
  font-size: 14px;
  font-weight: 600;
}

.custom-range__item:first-child {
  width: 100%;
  @include for-size(tablet-portrait-up) {
    width: 20%;
  }
}

.custom-range__item:last-child {
  width: 35%;
}

.custom-range__item:nth-child(2) {
  max-width: 100px;
  width: 100%;
  text-align: center;
  border-radius: 5px;
  border: 1px solid #e6e6e6;
  padding: 10px 20px;
}
