.panel {
  padding: $padding-and-margin 0;

  @include grid-container();
  &__inner {
    @include grid-row();
  }
  &__sidebar {
    @include grid-col(
      (
        xs: 12,
        sm: 3
      )
    );
  }
  &__main {
    max-width: 100%;

    @include grid-col(
      (
        xs: 12,
        sm: 9
      )
    );

    overflow: hidden;
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    &-item {
      margin-bottom: var(--padding-and-margin);

      @include for-size(tablet-portrait-up) {
        margin-bottom: calc(var(--padding-and-margin) * 2);
      }

      &-text {
        margin-right: var(--padding-and-margin);
        font-family: $mainFont-bold;
        font-weight: $mainFontWeight-bold;

        @include for-size(tablet-portrait-down) {
          display: block;
          margin-bottom: var(--padding-and-margin);
        }
      }
    }
  }
  &__items-item {
    & .link {
      text-transform: initial;
    }
    & .link.link--white {
      margin-right: var(--padding-and-margin);
    }
  }
  & .tabs {
    margin-bottom: var(--padding-and-margin);
    &__item {
      font-size: 0.875rem;
    }
  }
}
