
.menu {
  $root: &;

  &__burger {
    $burger: #{$root}__burger;
    width: 50px;
    height: 50px;
    right: 20px;
    top: 50px;
    position: fixed;
    cursor: pointer;
    z-index: 99;

    &__span {
      position: absolute;
      height: 5px;
      width: inherit;
      background-color: #000;
      transition: all 0.2s;
      &:before {
        @extend #{$burger}__span;
        content: "";
        top: -10px;
      }
      &:after {
        @extend #{$burger}__span;
        content: "";
        top: 10px;
      }
    }
    &--open {
      #{$burger}__span {
        background-color: transparent;
        &:before,
        &:after {
          background-color: #fff;
          transform-origin: 0 50%;
        }
        &:before {
          transform: translateX(7px) translateY(-5px) rotate(45deg);
        }
        &:after {
          transform: translateX(7px) translateY(10px) rotate(-45deg);
        }
      }
    }
  }
  &__nav {
    position: absolute;
    top: 0;
    right: -25%;
    height: 100%;
    width: 25%;
    overflow: scroll;
    background-color: #333;
    transition: all 0.3s;
    z-index: 98;

    @media screen and (max-width: 768px) {
      width: 90%;
      right: -90%;
    }

    &--open {
      right: 0;
    }
  }
  &__list {
    list-style: none;
    position: absolute;
    top: 60px;
    left: 25px;

    &__item {
      margin-left: 0;
      margin-top: 25px;
      font-size: 30px;
      transition: all 0.3s ease-in-out;
    }
  }
  &__link {
    text-decoration: none;
    color: #ddd;
    &:hover {
      text-decoration: inherit;
      color: #fff;
    }
  }
}
